import {
  AnimatedKeyboardAvoidingViewRef,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  UIInput,
} from '@true/ui';
import React, {memo, useCallback, useRef} from 'react';
import {
  getUser,
  goBack,
  styled,
  ToastService,
  useAsyncFn,
  useNavigationParams,
} from '@true/core';
import Animated, {
  runOnJS,
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
} from 'react-native-reanimated';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {
  ETypeChannel,
  ICreateChannel,
  initCreateChannel,
} from '@/store/channels/type';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {requestCreateChannel} from '@/store/channels/function';
import LoadingModal from '@/components/LoadingModal';

export interface CreateChannelScreenParams {
  id: number | string;
}
export const CreateChannelScreen = memo(function () {
  const params = useSharedValue<ICreateChannel>(initCreateChannel);
  const {id} = useNavigationParams<CreateChannelScreenParams>();
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const typeChannelOptions = [
    {
      label: 'Public, Company-wide',
      value: ETypeChannel.COMPANY,
    },
    {
      label: 'Team or department',
      value: ETypeChannel.TEAM,
    },
  ];
  const animatedActiveBtn = useDerivedValue(() => {
    return !!params.value.name;
  }, []);

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      if (id !== -1) {
        await requestCreateChannel(params.value);
      } else {
        await requestCreateChannel(params.value);
        ToastService.show('Create channel successfully');
      }
      goBack();
    }, [id, params]);
  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  return (
    <FullScreenWrapper>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <HeaderCreatePost
          loading={loading}
          onPress={onPressSubmit}
          active={animatedActiveBtn}
          title={'Create channel'}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <UIInput.Text
              title={'Channel name'}
              keyName={'name'}
              params={params}
              placeholder={'---'}
              onChangeValue={onChangeValue}
              required
            />
            <UIInput.Text
              title={'Channel path'}
              keyName={'path'}
              params={params}
              placeholder={'---'}
              onChangeValue={onChangeValue}
              required
            />
            <UIInput.Selector
              title={'Channel type'}
              keyName={'metatype'}
              placeholder={'---'}
              onChangeValue={onChangeValue}
              options={typeChannelOptions}
              required
            />
            <UIInput.UserPicker
              title={'Channel admins'}
              type={'user'}
              keyName={'owners'}
              onChangeValue={onChangeValue}
              multiple
            />
            <UIInput.UserPicker
              title={'Channel members'}
              type={'user'}
              params={params}
              keyName={'followers'}
              onChangeValue={onChangeValue}
              multiple
            />
            <UIInput.Text
              title={'Short description'}
              keyName={'content'}
              placeholder={'---'}
              params={params}
              onChangeValue={onChangeValue}
              required
            />
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
      </MyKeyboardAvoidingView>
    </FullScreenWrapper>
  );
});
const SContainer = styled(Animated.View)`
  flex: 1;
  gap: 12px;
  padding: 12px 16px;
`;

const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
